
import Panel from 'vux/src/components/panel/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Radio from 'vux/src/components/radio/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import CellFormPreview from 'vux/src/components/cell-form-preview/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'


import {orderState,orderMsg,complate,orderStateOfobj} from "@/api/order"

export default {
props: {
dataset: Object,
type: Number 
},
data() {
var dataset = {
options: [],
val: "",
riderType: ["", "顺风车", "装车", "速递", "快车"],
mobile: "",
url: "tel://",
stateEnum: ["取消", "待出行", "完成交易", "正在进行", "过期", "已接单"]
};
return dataset;
},
computed: {},
created() {},
mounted() {},
watch: {
dataset: function(old, news) {
if(this.dataset==null)return;
this.getData();
}
},
methods: {
getData() {

if(this.dataset.data==undefined)return;
this.options = [];
if (this.type == 1) {
this.ordersInfo();
this.val = this.dataset.data.driverprice;
} else if (this.type == 2) {
this.ordersInfo();
this.driverInfo();
this.val = this.dataset.data.userprice;
}

this.options.push({
label: "订单状态",
value: this.stateEnum[this.dataset.data.state + 1]
});
},
dateFormat: function(time) {
var date = new Date(time);
if(time==null)date=new Date();
var year = date.getFullYear();

var month =
date.getMonth() + 1 < 10
? "0" + (date.getMonth() + 1)
: date.getMonth() + 1;
var day = date.getDate() < 10 ? "0" + date.getDate() : date.getDate();
var hours =
date.getHours() < 10 ? "0" + date.getHours() : date.getHours();
var minutes =
date.getMinutes() < 10 ? "0" + date.getMinutes() : date.getMinutes();
var seconds =
date.getSeconds() < 10 ? "0" + date.getSeconds() : date.getSeconds();

return (
year +
"-" +
month +
"-" +
day +
" " +
hours +
":" +
minutes +
":" +
seconds
);
},
ordersInfo() {
var orders = this.dataset.data.ordersInfo;
if (orders == undefined || orders == null || orders == "") return;
orders = JSON.parse(orders);
this.mobile = orders.rider;
this.url = "tel://" + this.mobile;
this.options.push({ label: "用户称呼", value: orders.nickname });
this.options.push({ label: "联系方式", value: orders.rider });
this.options.push({
label: "乘车时间",
value: this.dateFormat(orders.startDate)
});
this.options.push({ label: "上车地址", value: orders.startingPoint });
this.options.push({ label: "下车地址", value: orders.endingPoint });
this.options.push({
label: "订单类型",
value: this.riderType[orders.riderType]
});
if (orders.riderType == 3) {
this.options.push({ label: "收件人称呼", value: orders.addresseeCall });
this.options.push({
label: "收件人联系方式",
value: orders.addresseeMobile
});
}
if (orders.riderType == 1 || orders.riderType == 2)
this.options.push({ label: "乘车人数", value: orders.personNum });
},
driverInfo() {
var driver = this.dataset.data.driverInfo;
if (driver == undefined || driver == null || driver == "") return;
driver = JSON.parse(driver);
this.mobile = driver.mobile;
this.url = "tel://" + this.mobile;

this.options.push({ label: "司机称呼", value: driver.usercall });
this.options.push({ label: "车牌号", value: driver.carcard });
this.options.push({ label: "联系方式", value: driver.mobile });
this.options.push({ label: "车身颜色", value: driver.carColor });
},
call: function() {
window.location.href = this.url;
},
jd(){
var dataset=this;
var driver = JSON.parse(this.dataset.data.driverInfo);
var orders = JSON.parse(this.dataset.data.ordersInfo);
console.log(orders);
orderStateOfobj(this.dataset.data.id,{state:"2",arrivalTime:this.dateFormat()}).then(response=>{
console.log(response);
if(response){
dataset.$vux.toast.show({ text: '开始接单，5分钟后乘客没到达可以取消' });
var msg = "您的订单，司机已经到达接车地点，5分钟后乘客没到达司机可以免费取消，请注意。\r\n车牌号："+ driver.carcard+"\r\n司机称呼：" + driver.usercall + "\r\n联系方式:" + driver.mobile;
orderMsg(orders.openid,msg);
dataset.dataset.data.state=2;
}else{
alert("接单失败");
}
});
},
desc(){
var dataset=this;
var openid=JSON.parse(this.dataset.data.ordersInfo.openid);
var userinfo = JSON.parse(this.dataset.data);
orderState(this.dataset.data.id,"-1").then(response=>{
if(response){
dataset.$vux.toast.show({ text: '订单已经被取消' });
var msg = "您从" + userinfo.startingPoint + "到" + userinfo.endingPoint + "的订单已经被取消。";
orderMsg(openid,msg);
dataset.dataset.data.state="-1";
}else{
alert("接单失败");
}
});
},
complate(){
var dataset=this;
var ordersInfo=JSON.parse(this.dataset.data.ordersInfo);
var userinfo = this.dataset.data;
complate(this.dataset.data).then(function(result){
if(result!=""){
dataset.$vux.toast.show({ text: '订单已经完成，等待用户确认' });
dataset.dataset.data.state=1;
var msg = "您"+dataset.dataset.desc+"已经完成。如没有疑问点击连接支付：http://baibiancx.top/index.html#/successpage?sing="+result;
console.log(msg);
orderMsg(ordersInfo.openid,msg);
}else{
dataset.$vux.toast.show({ text: '订单提交失败' });
}
});
}
},
components: {
Panel,
Group,
Radio,
XHeader,
CellBox,
Cell,
CellFormPreview,
XButton
}
};



